import "./staff.scss"

const $$ = window.$$

const arrowTpls = {
  prevArrow: '<button type="button" class="Staff--arrow Staff--arrow__prev"><span></span></button>',
  nextArrow: '<button type="button" class="Staff--arrow Staff--arrow__next"><span></span></button>',
}

let $sliders = null
let $slidersSources = [$$("#StaffBirthdaySlider").html(), $$("#StaffNewSlider").html()]

const processSliders = () => {

  if ($$(window).width() >= 992) {

    $sliders = $$("#StaffBirthdaySlider, #StaffNewSlider").slick({
      slidesToShow: 3,
      arrows: true,
      dots: false,
      ...arrowTpls,
      responsive: [
        {
          breakpoint: 1201,
          settings: {
            slidesToShow: 4,
          }
        }
      ]
    })

  } else {
    if ($sliders) {
      $$("#StaffBirthdaySlider, #StaffNewSlider").slick("unslick")
      $$("#StaffBirthdaySlider").html($slidersSources[0])
      $$("#StaffNewSlider").html($slidersSources[1])
      $sliders = null
    }
  }

}

processSliders()

window.addEventListener('resize', function(event) {
  processSliders()
}, true)

$$(document).tooltip({
  items: ".Staff--tooltip__source",
  position: {
    my: "left top",
    at: "left top",
    using: function (position, feedback) {
      const $html = feedback.target.element.find(".Staff--tooltip__content").html()
      const width = feedback.target.element.width()
      $$(this).html($html).css({top: position.top -8, left: position.left - 8}).css("width", width).addClass("Staff--tooltip")
    }
  },
})

